body {
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  text-align: center;
}

h1 {
  margin: 0;
  margin-bottom: 0.5rem;
  color: darkblue;
}

.input {
  border: 1px solid #333;
  border-radius: 0.3em;
  padding: 0.25rem;
  width: 7em;
  margin-top: 20px;
}

select {
  margin-left: 0.5rem;
}

.equals {
  font-weight: bold;
  font-size: 2rem;
}

a {
  opacity: 0;
}

.card {
  position: relative;
  background: lightblue;
  padding: 3em;
  border-radius: 10px;
  border: 1px solid darkblue;
  box-shadow: 5px 5px 5px 5px darkgray;
  animation: twirl 1s forwards linear;
}

@keyframes twirl {
  0% {
    transform: translateY(-300px);
  }
  50% {
    transform: translateY(300px);
  }
}
.wrapper {
  position: absolute;
  top: 0;
  left: 0;
  background: darkblue;
  width: 100%;
  border-radius: 10px 10px 0 0;
  padding: 10px 0;
  color: #fff;
}
